.header {
  justify-content: space-between;
  margin: 0 auto;
  padding-left: 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__list {
  flex-direction: row;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-align: center;
  color: #060b35;
  padding: 22.5px 0;
  text-decoration: none;
}

.nav__link:focus {
  outline: thin dotted;
}

.nav__ling:active, .nav__link:hover {
  color: #00acdc;
  outline: 0;
}

.nav {
  height: 60px;
  margin-right: 50px;
}

.nav__item {
  text-transform: uppercase;
  margin-right: 20px;
  font-weight: 500;
  display: flex;
}

.nav__item:last-child {
  margin-right: 0;
}

.logo {
  padding-top: 10px;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 4px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 0 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  transition: all .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.card__content {
  margin: 40px 0 16px;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: block;
}

.card__code {
  color: #616070;
  margin-top: 5px;
  display: block;
}

.card__review {
  color: #060b35;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 18px;
  display: flex;
}

.card__price {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border-color: #0000;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 15px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
}

.card__button:hover {
  color: #00acdc;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
}

.catalog {
  grid-template: repeat(2, 1fr) / repeat(4, 1fr);
  justify-items: center;
  gap: 46px 48px;
  max-width: 944px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width <= 488px) {
  .catalog {
    grid-template: 1fr / 1fr;
  }
}

@media screen and (width >= 489px) and (width <= 768px) {
  .catalog {
    grid-template: 1fr 1fr / 1fr 1fr;
  }
}

@media screen and (width >= 769px) and (width <= 1023px) {
  .catalog {
    grid-template: 1fr 1fr / repeat(3, 1fr);
  }
}
